import React, { useEffect } from 'react';
import { useSelector, useDispatch } from "react-redux";
import { getProducts, selectAllProducts } from "../slicer/Products";


const Products = () => {

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getProducts())
  }, [dispatch]);
  const datas = useSelector(selectAllProducts);
  console.log(datas)

  const dataDisplay = datas?.map((pro) => {
    return (
      <div className="col-md-3">
        <div className="card product-card">
          <div className="image-container">
            <div className="first">
              <div className="d-flex justify-content-between align-items-center">
                {/* <span className="discount">-25%</span> */}
                <span className="wishlist"><i className="fa fa-heart-o"></i></span>
              </div>
            </div>
            <img src={pro.images} alt="ppssp" className="img-fluid rounded thumbnail-image" />
          </div>
          <div className="product-detail-container p-2">
            <div className="d-flex justify-content-between align-items-center">
              <h5 className="dress-name">{pro.name}</h5>
              <div className="d-flex flex-column mb-2">
                <span className="new-price">$ {pro.price}</span>

              </div>
            </div>

            <div className="d-flex justify-content-between align-items-center pt-1">
              <div>
                <i className="fa fa-star-o rating-star"></i>
                <span className="rating-number">{pro?.quantity}</span>
              </div>

              <a  className="buy" style={{textDecoration:"none"}} href={`/products/${pro?._id}`}>
              <span className="buy"> purchase </span>
              </a>
            </div>
          </div>
        </div>

      </div>
    )
  })
  return (
    <div className="container mt-5 px-1">
      {/* <hr/> */}
      <h2 className='text-center text-success mb-5 text-styled'> Explore Our Utils</h2>
      <div className="row py-5 mb-5">
        {dataDisplay}
        {/* Repeat the above structure for the remaining columns */}
      </div>
      {/* <hr/> */}
    </div>
  );
};

export default Products;