import React, { useRef, useEffect, useState } from "react";
import upload from "../utills/cloudinaryUpload"
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { handleInput, validateEmpty, Notifier } from "../utills/InputHelpers";
import { createProducts, reseter } from "../slicer/Products";
import { selectAllCategories } from "../slicer/Category";

const ProductAdd = () => {
  const { status, message } = useSelector((state) => state?.products);
  const [formErrors, setFormErrors] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);
  const [singleFile, setSingleFile] = useState(undefined);
  const [uploading, setUploading] = useState(false);
  const [formData, setFormData] = useState({
    desc: "",
    name: "",
    brand: "",
    catId: "",
    price: "",
    quantity: "",
    images: ""
  });
  const referal = useRef();
  const dispatch = useDispatch();
  const navigate = useNavigate();



  //get catgories
  const categories = useSelector(selectAllCategories);
  const categoriesOption = !categories
    ? ""
    : Array.from(categories)
      .sort((a, b) => {
        if (a.name < b.name) return -1;
        if (a.name > b.name) return 1;
        return 0;
      })
      .map((category) => {
        return (

          <option key={category._id} value={category._id}>
            {category.name}
          </option>
        );
      });



  const handleUpload = async (e) => {
    e.preventDefault();
    setUploading(true);
    try {
      const cover = await upload(singleFile);
      setFormData((prev) => ({
        ...prev,
        "images": cover,
      }));
      setUploading(false);
      Notifier("Image Uploaded", "success");
    } catch (err) {
      console.log(err);
      Notifier(err, "error");
    }
  };

  useEffect(() => {
    referal.current();
  }, [formErrors, status, message, navigate, dispatch]);

  const handleProduct = (e) => {
    setFormErrors({})
    e.preventDefault();
    setFormErrors(validateEmpty(formData));
    setIsSubmit(true);
  };

  const reset = () => {
    setFormData({
      desc: "",
      name: "",
      brand: "",
      catId: "",
      price: "",
      quantity: "",
      images: ""
    });
  };

  const dispatchSignup = () => {
    if (Object.keys(formErrors).length === 0 && isSubmit) {
      dispatch(createProducts(formData));
      setIsSubmit(false);
    }

    if (status === "succeeded") {
      Notifier(message, "success");
      reset();
      dispatch(reseter());
      setIsSubmit(false);
    }
    if (status === "failed") {
      Notifier(message, "error");
      dispatch(reseter());
    }
  };
  referal.current = dispatchSignup;

  return (
    <div className="wrapper">

      <div className="text-center mt-4 name text-styled"> Add Utilities </div>
      <form className="p-3 mt-3">
        <div className="form-field d-flex align-items-center">
          <input
            type="text"
            name="name"
            id="namex"
            placeholder="enter product name"
            value={formData.name}
            onChange={(e) => handleInput(e, setFormData)}
          />
        </div>
        <div className="form-field d-flex align-items-center">
          <input
            type="text"
            name="brand"
            id="brand"
            placeholder="enter product brand"
            value={formData.brand}
            onChange={(e) => handleInput(e, setFormData)}
          />
        </div>
        <div className="form-field d-flex align-items-center">
          <input
            type="text"
            name="price"
            id="price"
            placeholder="enter product price"
            value={formData.price}
            onChange={(e) => handleInput(e, setFormData)}
          />
        </div>
        <div className="form-field d-flex align-items-center">
          <select
            name="catId"
            id="catId"
            onChange={(e) => handleInput(e, setFormData)}
          >
            <option>Select reservation Category</option>
            {categoriesOption}
            {/* Add more options as needed */}
          </select>
        </div>
        <div className="form-field d-flex align-items-center">
          <input
            type="text"
            name="quantity"
            id="quantity"
            placeholder="enter product quantity available"
            value={formData.quantity}
            onChange={(e) => handleInput(e, setFormData)}
          />
        </div>
        <div className="form-field d-flex align-items-center">
          <input
            type="file"
            required
            name="images"
            id="images"
            onChange={(e) => setSingleFile(e.target.files[0])}
          />

          {!formData.images && (
            <button className='btn' onClick={(e) => handleUpload(e)}>
              {uploading ? "uploading" : "Upload"}
            </button>

          )}
        </div>
        <div className="form-field d-flex align-items-center">
          <textarea
            name="desc"
            id=""
            placeholder="brief product descriptions"
            cols="0"
            rows="5"
            required
            value={formData.desc}
            onChange={(e) => handleInput(e, setFormData)}
          ></textarea>
        </div>

        <button className="btn mt-3" onClick={handleProduct}>Upload</button>
      </form>
    </div>
  );
};

export default ProductAdd;
